.content-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.content-page {
  align-self: flex-end;
}

.text-ellipsis {
  // min-width: 100px;
  overflow: hidden; /* 넘치는 내용 숨기기 */
  white-space: nowrap; /* 텍스트를 한 줄로 표시 */
  text-overflow: ellipsis; /* 말줄임표 표시 */
  margin-bottom: 10px;
}
